.projects {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 5vh 10vw;
  background-color: #1f2430;
  color: aliceblue;
}
h2 {
  justify-self: center;
}
.project {
  display: flex;
  justify-content: center;
  color: white;
  margin: 30px;
}
.projects > hr {
  width: 70vw;
  color: white;
  height: 2px;
  margin: 10px;
}
.project > div {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 12px;
}

.project > div > h3 {
  width: 80%;
  padding: 5px 0px;
  text-align: left;
  font-size: 13px;
  font-weight: 100;
  color: #6ce2e2;
}
.project > div > h3 > a {
  color: #8f54d9;
}
.project-iframe {
  width: 80vw;
  max-width: 400px;
  height: max-content;
  min-height: 240px;
  margin: 0px 20px;
  padding: 05px;
  border-radius: 5px;
  background-color: black;
}

@media screen and (max-width: 750px) {
  .project {
    flex-direction: column;
  }
  .project-iframe {
    padding: 10px 10px;
  }
}
